import {createElement} from '../render.js';
import {calculateEventDuration} from '../utils.js';

function createRoutePointTemplate(point, destinations, offers) {
const {basePrice, dateFrom, dateTo, isFavorite, type} = point
const iconSrc = type.toLowerCase();
const typeOffers = offers.find((offer) => offer.type.toLowerCase() === type.toLowerCase()).offers;
const pointOffers = typeOffers.filter((typeOffer) => point.offers.includes(typeOffer.id));
const pointDestination = destinations.find((dest)=>dest.id === point.destination);
const offersMarkup = pointOffers.length > 0 ? `
<h4 class="visually-hidden">Offers:</h4>
<ul class="event__selected-offers">
  ${pointOffers.map((offer) => (
    `<li class="event__offer">
      <span class="event__offer-title">${offer.title}</span>
      &plus;&euro;&nbsp;
      <span class="event__offer-price">${offer.price}</span>
    </li>`
  )).join('')}
</ul>` : '';
  return (
    `<li class="trip-events__item">
    <div class="event">
      <time class="event__date" datetime="${dateFrom}">${new Date(dateFrom).toLocaleDateString('en-US', {day: '2-digit',month: 'short'}).toUpperCase()}
      </time>
      <div class="event__type">
        <img class="event__type-icon" width="42" height="42" src="img/icons/${iconSrc}.png" alt="Event type icon">
      </div>
      <h3 class="event__title">${type} ${pointDestination?.name || ''}</h3>
      <div class="event__schedule">
        <p class="event__time">
          <time class="event__start-time" datetime="${dateFrom}">${new Date(dateFrom).getUTCHours().toString().padStart(2, '0')}:${new Date(dateFrom).getUTCMinutes().toString().padStart(2, '0')}</time>
          &mdash;
          <time class="event__end-time" datetime="${dateTo}">${new Date(dateTo).getUTCHours().toString().padStart(2, '0')}:${new Date(dateTo).getUTCMinutes().toString().padStart(2, '0')}</time>
        </p>
        <p class="event__duration">${calculateEventDuration(dateFrom, dateTo)}</p>
      </div>
      <p class="event__price">
        &euro;&nbsp;<span class="event__price-value">${basePrice}</span>
      </p>

      ${offersMarkup}

      <button class="event__favorite-btn ${isFavorite ? 'event__favorite-btn--active' : ''}" type="button">
        <span class="visually-hidden">Add to favorite</span>
        <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
          <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
        </svg>
      </button>
      <button class="event__rollup-btn" type="button">
        <span class="visually-hidden">Open event</span>
      </button>
    </div>
  </li>>`
  );
}

export default class RoutePoint {
  constructor({point, destinations, offers}) {
    this.point = point;
    this.destinations = destinations;
    this.offers = offers;
  }
  getTemplate() {
    return createRoutePointTemplate(this.point, this.destinations, this.offers);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
